const NewsLetter = ({ name, link }) => (
  <div>
    <p>
      <a href={link} target="_blank">
        Subscribe
      </a>{' '}
      to the {name} newsletter to see more like this.
    </p>
    <style jsx>{`
      p {
        margin: 20px 0;
        padding: 15px 10px;
        background-color: #8bc34a;
        color: #fff;
        font-size: 18px;
        text-align: center;
      }

      p a {
        color: #fff;
        border-bottom-color: #fff;
        font-weight: 600;
      }
    `}</style>
  </div>
);

export default NewsLetter;
export const Cryptocurrency = () => (
  <NewsLetter
    name="cryptocurrency"
    link="https://tinyletter.com/cryptocurrency"
  />
);



